@use "./variables";
@use './mixins';

body {
    font-family: "Roboto", sans-serif;
    color: variables.$text-color-secondary;
    background: variables.$smoky-white;
    font-size: 14px;
    padding: 30px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.42857143
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
}

a {
    color: darken(variables.$theme-color, 15%);

    text-decoration: none;
    &:hover {
        text-decoration: underline;
        color: darken(variables.$theme-color, 30%);
    }

    &:focus {
        text-decoration: none;
    }
}

p {
    line-height: 1.5;
}


.list-unstyled {
    padding-left: 0;
    list-style: none;
}

.wrapper {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    background: variables.$theme-color;
    max-width: 1000px;
    margin: 0 auto;
    position: relative;
    @include mixins.box-shadow(0px 2px 4px rgba(0, 0, 0, 0.1));
}


.sidebar-wrapper {
    grid-column: span 3;
    order: variables.$sidebar-order;
    background: variables.$theme-color;
    color: #fff;

    a {
        color: #fff;
    }

    .profile-container {
        padding: 30px;
        padding-top: 80px;
        padding-bottom: 80px;
        //background: darken($theme-color, 10%);
        background: rgba(0, 0, 0, 0.2);
        text-align: center;
        color: #fff;
    }

    .name {
        font-size: 32px;
        font-weight: 900;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .tagline {
        color: rgba(256, 256, 256, 0.6);
        font-size: 16px;
        font-weight: 400;
        margin-top: 0;
        margin-bottom: 0;
    }

    .profile {
        margin-bottom: 15px;
    }

    .avatar {
        //used for profile image
        max-width: 100px;
        margin-bottom: 15px;
        border: 0px solid #fff;
        border-radius: 100%;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    }

    .contact-list {

        .fas,
        .fab,
        .far {
            margin-right: 5px;
            font-size: 18px;
            vertical-align: middle;
        }

        li {
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .container-block {
        padding: 30px;
    }

    .container-block-title {
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 700;
        margin-top: 0;
        margin-bottom: 15px;
    }

    .degree {
        font-size: 14px;
        margin-top: 0;
        margin-bottom: 5px;
    }

    .education-container {
        .item {
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .meta {
            color: rgba(256, 256, 256, 0.6);
            font-weight: 500;
            margin-bottom: 0px;
            margin-top: 0;
        }

        .time {
            color: rgba(256, 256, 256, 0.6);
            font-weight: 500;
            margin-bottom: 0px;
        }
    }

    .languages-container {
        .lang-desc {
            color: rgba(256, 256, 256, 0.6);
        }
    }

    .languages-list {
        margin-bottom: 0;

        li {
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .interests-list {
        margin-bottom: 0;

        li {
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.main-wrapper {
    grid-column: span 7;
    order: variables.$resume-order;
    background: #fff;
    padding: 60px;

    .section-title {
        text-transform: uppercase;
        font-size: 20px;
        font-weight: 500;
        color: darken(variables.$theme-color, 15%);
        position: relative;
        margin-top: 0;
        margin-bottom: 20px;
    }

    .section {
        margin-bottom: 60px;
    }

    .experiences-section,
    .recommendations-section,
    .educations-section {
        .item {
            margin-bottom: 30px;
        }
    }

    .upper-row,
    .second-upper-row {
        position: relative;
        overflow: hidden;
        margin-bottom: 2px;
        display: flex;
    }

    .job-title,
    .degree,
    .cert-title,
    .recommendation-person {
        color: variables.$text-color;
        font-size: 16px;
        margin-top: 0;
        margin-bottom: 0;
        font-weight: 500;
        flex: 75%;
    }

    .time,
    .cert-url {
        position: absolute;
        right: 0;
        top: 0;
        color: variables.$text-grey;
        flex: 25%;
    }

    .company,
    .recommendation-role,
    .university,
    .cert-org {
        margin-bottom: 10px;
        color: variables.$text-grey;
    }

    .project-title {
        font-size: 16px;
        font-weight: 400;
        margin-top: 0;
        margin-bottom: 5px;
        color: darken(variables.$theme-color, 15%);
    }

    .projects-section {
        .intro {
            margin-bottom: 30px;
        }

        .item {
            margin-bottom: 15px;
        }
    }

    .oss-section {
        .intro {
            margin-bottom: 30px;
        }

        .item {
            margin-bottom: 15px;
        }
    }

    .publication-title {
        font-size: 16px;
        font-weight: 400;
        color: variables.$text-color;
        margin-bottom: 1px;
    }

    .publication-authors {
        font-style: italic;
        color: variables.$text-grey;
        margin-bottom: 1px;
    }

    .publications-section {
        .intro {
            margin-bottom: 30px;
        }

        .item {
            margin-bottom: 15px;
        }
    }
}